      // -moz-animation: CSSGradientAnimator 30s ease infinite;
      // animation: CSSGradientAnimator 30s ease infinite;
      // text-decoration: underline;
    }
  }
}

@-webkit-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@-moz-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

