.infinite-card {
  position: relative;
  min-height: 580px;
  max-height: 580px;
  overflow-y: scroll;
  position: relative;
  background: transparent linear-gradient(90deg, #36477B 0%, #233460 100%) 0% 0% no-repeat padding-box;
  border-radius: 11px;
  opacity: 1;
  width: 100%;
  color: white;
  border: none;

  .infinite-card-center-content {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 400px;
    bottom: 0;
  }

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-track {
    border-radius: 10px;
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.226);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: transparent;
  }

  .infinite-card-item {
    padding: 5px;
    margin-bottom: 0.5rem;

    &:hover {
      background-color: #0000002b;
      cursor: pointer;
    }
  }

  .infinite-card-header {
    position: -webkit-sticky;
    /* Safari & IE */
    position: sticky;
    top: 0;
    background: rgba(0, 0, 0, 0.055);
    left: 0;
    z-index: 99 !important;
    background: transparent linear-gradient(90deg, #36477B 0%, #233460 100%) 0% 0% no-repeat padding-box;
    padding-top: 2rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .infinite-card-body {
    top: 10px;
    padding-bottom: 2rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .infinite-card-footer {
    display: none;
    position: absolute;
    bottom: 20px;
    left: 0;
    text-align: center;
    margin: 0 auto;
    width: 100%;
  }
