/* Start Table */
.tuknuk-table-theme {

  .ant-table,
  .ant-table-content {
    background-color: transparent !important;
    color: white !important;
  }

  .ant-table-thead {
    .ant-table-cell {
      background-color: #06112d !important;
      color: white !important;
      border: none !important;
    }
  }

  .ant-table-tbody {
    .ant-table-cell {
      background-color: transparent !important;
      color: white !important;
      border: none !important;
    }

    .ant-table-row {
      background-color: #253457 !important;

      &:nth-child(odd) {
        background-color: #1b2949 !important;
        color: white !important;
      }
    }
  }

  &:where(.css-dev-only-do-not-override-ixblex).ant-table-wrapper .ant-table-thead>tr>th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before,
  &:where(.css-dev-only-do-not-override-ixblex).ant-table-wrapper .ant-table-thead>tr>td:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before {
    background-color: transparent;
  }

  thead {
    border-top-left-radius: 105px !important;
  }
}

/* End Table */

/* Modal lateral */
@media (min-width: 992px) {
  .modal-lateral {
    min-width: 100vw !important;
    width: 100vw;
    right: 0 !important;
    top: 0 !important;
    position: relative;

    .ant-modal-content {
      position: absolute;
      right: 0;
      top: 0;
      min-width: 60vw;
      min-height: 100vh;
      border-radius: 0px;
      background-color: #1e2f4f !important;

      .ant-modal-title {
        background-color: #1e2f4f !important;
      }

      div {
        color: white !important;
      }

      .ant-modal-close-x {
        font-size: 20px !important;
      }

      .ant-modal-close {
        position: absolute;
        background-color: #257fad !important;
        /* width: 22px; */
        height: 50px;
        width: 60px;
        left: -50px;
        top: 50px;
        color: white;
        border-top-left-radius: 45%;
        border-bottom-left-radius: 45%;
        z-index: -1 !important;

        &:hover {
          left: -55px;
          .ant-modal-close-x {
            font-size: 25px !important;
          }
        }
      }

      .custom-modal-lateral-footer {
        position: absolute;
        bottom: 50px;
        width: 100%;
        left: 0;
        right: 0;
        background-color: transparent;
      }
    }
  }
}

